// var reg = "Registration script"
import $ from 'jquery'
// import jQuery from 'jquery'
// import Inputmask from "inputmask";

var reg = function(){
	return {
		clickNext: function(){
			var counter = 0
			$('.reg-window__next-btn').on('click', function(){
				$('.reg-window__box').hide();
				counter++;
				console.log('---click')
				$('.reg-window__box').each(function(index){
					if(index === counter)
						$(this).show()
				})
			})
		},
		initialState: function(){
			$('.reg-window__box').hide();
			$('.reg-window__box').each(function(index){
				if(index === 0)
					$(this).show()
			})
		},
		init: function(){
			this.clickNext();
			this.initialState();
		}
	}
}
export default reg



